import {memberInfo,comUserInfo} from '@/http/user'
export default{
	data(){
		return {
			index:0, //0 微信，1 支付宝
			payTypeList:['微信','支付宝'],
			payType:'微信',
			obj:{
			  "idCard": "",
			  "payNo": "",
			  "phone": "",
			  "surname": "",
			  "wxNo": ""
			}
		}
	},
	onShow(){
		this.init()
	},
	methods:{
		changType(e){
			console.log('e',e)
			this.index =e.detail.value
		},
		init(){
			this.memberInfoFun()
		},
		comUserInfoFun(){
			comUserInfo(this.obj).then(data=>{
				if(data.data.retCode===0){
					uni.showToast({
						title: data.data.retMsg,
						duration: 2000,
						icon:'none'
					});
				}else{
					uni.showToast({
						title: data.data.retMsg,
						duration: 2000,
						icon:'none'
					});
				}
			}).catch(e=>{
				uni.showToast({
					title: e.data.retMsg,
					duration: 2000,
					icon:'none'
				});
			})
		},
		memberInfoFun(){
			memberInfo().then(data=>{
				if(data.data.retCode===0){
					this.obj = data.data.result
					console.log('obj:-->',this.obj)
					if(!this.obj.wxNo){
						this.obj.wxNo=''
					}
					if(!this.obj.idCard){
						this.obj.idCard=''
					}
					if(!this.obj.payNo){
						this.obj.payNo=''
					}
				}else{
					uni.showToast({
						title: data.data.retMsg,
						duration: 2000,
						icon:'none'
					});
				}
			}).catch(e=>{
				uni.showToast({
					title: e.data.retMsg,
					duration: 2000,
					icon:'none'
				});
			})
		},
		save(){
			this.comUserInfoFun()
		}
	}
}
